<!--
 * @Description: 分销员单据  按邀请奖励查
 * @Author: your name
 * @Date: 2019-11-28 
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="app-container">
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="160px">
          <el-form-item label="订单号：">
            <el-input v-model="list.orderNo"
                      clearable
                      placeholder="订单号"></el-input>
          </el-form-item>
          <el-form-item label="单据状态：">
            <el-select v-model="list.orderStauts"
                       placeholder="请选择">
              <el-option v-for="item in settlementList"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="下单时间：">
            <el-date-picker v-model="list.registerTime"
                            type="datetimerange"
                            value-format="yyyy-MM-dd HH-mm-ss"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             shadow="never">
      <span style="margin-left:20px;color: #f59a23;font-weight: 500;font-size:18px">分销员昵称：<span>{{this.$route.query.userName}}</span></span>
      <span style="margin-left:20px;color: #f59a23;font-weight: 500;font-size:18px">分销员手机号：<span>{{this.$route.query.cellPhone}}</span></span>
      <span style="margin-left:20px;color: #f59a23;font-weight: 500;font-size:18px">分销员所属分组：<span>{{this.$route.query.groupName}}</span></span>
      <!-- <span style="margin-left:20px;color: #4B7902;font-weight: 500;font-size:16px">温馨提示：业务员的订单成功支付后才可以查到；已关闭的订单不结算</span> -->
      <div style="margin:10px 0 0 20px;color: #f59a23;font-weight: 500;font-size:16px;line-height:32px">温馨提示：业务员的订单成功支付后才可以查到；未结算时，买家退款成功后，生成对应的待结算退款单据，与订单一并结算；</div>
    </el-card>
    <el-card class="table-container"
             shadow="never">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                @current-change="toggleRowExpansion"
                @cell-mouse-enter="onRowClick"
                @expand-change="getChaildren"
                border>
        <el-table-column type="expand"
                         default-expand-all="false">
          <template slot-scope="scope">
            <el-table :header-cell-style="{background:'#F4F5F6',color:'#131D34',padding: '8px 0'}"
                      :data="scope.row.channelProductDtoList"
                      style="width: 100%;border: none; margin-top:-20px"
                      border>
              <el-table-column type="index"
                               width="50">
              </el-table-column>
              <el-table-column label="商品名称"
                               width="100"
                               show-overflow-tooltip
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.productName}}
                </template>
              </el-table-column>
              <el-table-column label="商品编码"
                               width="100"
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.productCode}}
                </template>
              </el-table-column>
              <el-table-column label="规格"
                               width="100"
                               show-overflow-tooltip
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.skuName}}
                </template>
              </el-table-column>
              <el-table-column label="数量"
                               width="100"
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.productCount}}
                </template>
              </el-table-column>
              <el-table-column label="购买实付单价"
                               width="120"
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.payAmount}}
                </template>
              </el-table-column>
              <el-table-column label="分佣类型"
                               width="220"
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.commissionTypeName}}
                </template>
              </el-table-column>
              <el-table-column label="订单类型"
                               width="220"
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.orderTypeName}}
                </template>
              </el-table-column>
              <el-table-column label="收益比例"
                               width="220"
                               align="center">
                <template slot-scope="scope">
                  {{scope.row.rate == '--'? '--' : scope.row.rate + '%'}}
                </template>
              </el-table-column>
              <el-table-column label="所得邀请奖励"
                               width="220"
                               align="center">
                <template slot-scope="scope">
                  <span v-if="scope.row.type == 1||scope.row.type == 2" style="color:#67C23A">￥{{scope.row.commission}}</span>
                  <span v-if="scope.row.type == 3||scope.row.type == 4" style="color:#F56C6C">￥{{scope.row.commission}}</span>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column label="订单号"
                         width="140"
                         align="center">
          <template slot-scope="scope">{{scope.row.orderNo}}</template>
        </el-table-column>
        <el-table-column label="用户昵称"
                         prop="userName"
                         width="130"
                         align="center">
        </el-table-column>
        <el-table-column label="手机号"
                         width="130"
                         align="center">
          <template slot-scope="scope">{{scope.row.cellPhone == '' ? '--': scope.row.cellPhone}}</template>
        </el-table-column>
        <el-table-column label="下单时间"
                         width="160"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="单据状态"
                         width="160"
                         prop="statusName"
                         align="center">
        </el-table-column>
        <el-table-column label="结算状态"
                         width="120"
                         prop="setStateName"
                         align="center">
        </el-table-column>
        <el-table-column label="分佣商品金额"
                         width="120"
                         align="center">
          <template slot-scope="scope">
            ￥{{scope.row.payAmount}}
          </template>
        </el-table-column>
        <el-table-column label="佣金提成"
                         width="120"
                         align="center">
          <template slot-scope="scope">
            ￥{{scope.row.commissionAmount}}
          </template>
        </el-table-column>
      </el-table>

      <div class="flex-row-between"
           style="margin-top:20px">
        <!-- 返回 -->
        <el-button type="primary"
                   size="small"
                   @click="goBack">返回上一页</el-button>
        <!-- 页码 -->
        <el-pagination background
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>

    <!-- 固定在下面 -->

  </div>
</template>
<script>
import { channelInviteOrderList } from '@/api/distributionManagement/performance'
export default {
  props: ["status", "shop", "user"],
  data() {
    return {
      list: {
        orderNo: '',
        orderStauts: '',
        registerTime: '',
      },
      id: '',
      tableList: [],
      multipleTable: [],
      orderId: '',
      listChaildren: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      listLoading: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      settlementList: [
        {
          value: '',
          label: '全部'
        },
        {
          value: '1',
          label: '待发货'
        },
        {
          value: '2',
          label: '已发货'
        },
        {
          value: '3',
          label: '已完成'
        },
        {
          value: '4',
          label: '已关闭'
        },
      ],
      value: '',
    }
  },
  created() {
    this.searchList()
  },
  mounted() {

  },
  methods: {
    //查询数据列表
    searchList() {
      this.loading = true
      channelInviteOrderList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        userId: this.$route.query.userId,
        orderStauts: this.list.orderStauts,
        orderNo: this.list.orderNo,
        beginTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          });
        }
      })
    },

    onRowClick(row, event, column) {
      //行点击消除new标记
      this.orderId = row.orderId
      this.orderType = row.orderType
    },

    getChaildren(row, expandedRows) {
      if (expandedRows.length > 1) {
        this.tableList.forEach(item => {
          if (item.orderId != row.orderId) {
            this.$refs.multipleTable.toggleRowExpansion(item, false)
          }
        })
        return
      }
      console.log(this.orderId, row.orderId)
      this.listLoading = true;
      channelUserOrderDetail({
        orderId: this.orderId,
        orderType: this.orderType
      }).then(response => {
        this.listLoading = false;
        this.listChaildren = response.data;
      })
    },
    //table  切换手风琴
    toggleRowExpansion(row) {
      // this.expends.push(row.orderId)//展开当前行的信息
    },

    //重置
    reset() {
      this.list = {
        orderNo: '',
        status: '',
        registerTime: '',
      }
      this.searchList()
    },

    //返回上一页
    goBack() {
      this.$store.dispatch('delView', this.$route)
      // this.$router.push({
      //   path: '/distributionManagement/performance'
      // })
      this.$router.go(-1)
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    startTime() {
      return this.status;
    },
  },
  watch: {
    startTime(newValue, oldValue) {
      this.searchList()
      console.log(newValue)
      return newValue;
    },
  }
}
</script>

<style lang="scss" scoped>
.app-container {
  padding: 20px;
}
.button_mian {
  margin: 5px;
}
.btn-add-export {
  float: right;
  margin-top: -8px;
}
.operate-container {
  margin-top: 10px;
}
.btn-add {
  float: right;
  margin-top: -8px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
.fixedB {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  background: #eee;
  z-index: 100;
  .el-pagination {
    display: flex;
    align-items: center;
    margin-right: 200px;
  }
}
</style>
